* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

header {
  background-color: var(--main-color);
  width: 100%;
  z-index: 10;
  padding: .5rem 1rem;
  position: fixed;
  box-shadow: 0 3px 6px 1px #00000080;
}

.nav-main {
  max-width: 992px;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.nav-logo {
  height: 65px;
  width: auto;
}

.nav-logo img {
  height: 100%;
}

.nav-links {
  align-items: center;
  display: flex;
}

.nav-links ul {
  flex-direction: row;
  align-content: space-around;
  display: flex;
}

.nav-links ul li {
  padding-left: 3rem;
}

main {
  background-color: var(--main-color-light);
}

.main-contain {
  min-height: 100vh;
  height: auto;
  max-width: 992px;
  background-color: #fff;
  margin: 0 auto;
  padding-top: 95px;
  box-shadow: 3px 0 6px 1px #00000040, -3px 0 6px 1px #00000040;
}

.work-area > img {
  object-fit: cover;
  opacity: .8;
  width: 100%;
  height: 100vh;
  margin-top: -95px;
}

.categories {
  flex-flow: wrap;
  justify-content: center;
  display: flex;
}

.container-cat {
  height: auto;
  width: 46%;
  min-width: 350px;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  display: flex;
  box-shadow: 0 1.5px 6px 1px #00000040;
}

.items-img {
  width: 100%;
  height: 120px;
  border-radius: 8px 8px 0 0;
  margin-bottom: .5rem;
  overflow: hidden;
}

.items-img img {
  object-fit: cover;
  opacity: .8;
  width: 100%;
}

@media (max-width: 600px) {
  .nav-main {
    flex-wrap: wrap;
    justify-content: center;
  }

  .nav-links ul li {
    padding: 0 2rem;
  }

  .work-area {
    padding-top: 30px;
  }

  .work-area > img {
    margin-top: -120px;
    right: 250px;
  }
}

